<template>
    <div>
        <el-card style="margin-bottom: 30px;" v-if="post_id !== 2" shadow="never">
            <el-form :inline="true" :model="condition" class="demo-form-inline delbtm">
                <el-select v-model="condition.structure_id" filterable placeholder="请选择组" clearable>
                    <el-option value="0" label="全部"></el-option>
                    <el-option v-for="item in groupList.list" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>
                <el-select v-model="condition.uid" filterable placeholder="请选择组员" clearable>
                    <el-option value="0" label="全部"></el-option>
                    <el-option v-for="item in groupMemberList.list" :key="item.id" :label="item.realname"
                               :value="item.id">
                    </el-option>
                </el-select>
                <el-button icon="el-icon-search" @click="getReportList" type="primary">查询</el-button>
            </el-form>
        </el-card>
        <el-card shadow="never">
            <el-table :data="reportList.list" style="width: 100%">
                <el-table-column prop="time" label="日期" width="120" align="center">
                </el-table-column>
                <el-table-column label="营业支出" align="center">
                    <el-table-column label="放款金额" :render-header="renderHeader" prop="loan_amount_all" align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天放款的本金" placement="top-start">-->
                            <div class="cell">
                                {{ parseFloat(scope.row.loan_amount_all).toFixed(2) }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="服务费" :render-header="renderHeader" prop="service_amount_all" align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天放款的服务费" placement="top-start">-->
                            <div class="cell">
                                {{ parseFloat(scope.row.service_amount_all).toFixed(2) }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="应还总额" :render-header="renderHeader" prop="total_amount_all" align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天放款金额的应收回金额" placement="top-start">-->
                            <div class="cell">
                                {{ parseFloat(scope.row.total_amount_all).toFixed(2) }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="放款单数" :render-header="renderHeader" prop="loan_count" align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天放款的单数" placement="top-start">-->
                            <div class="cell">
                                {{ scope.row.loan_count }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="总支出" :render-header="renderHeader" prop="loan_amount_all" align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天放款本金" placement="top-start">-->
                            <div class="cell">
                                {{ parseFloat(scope.row.loan_amount_all).toFixed(2) }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                </el-table-column>
                <el-table-column label="营业收入" align="center">
                    <el-table-column label="已还金额" :render-header="renderHeader" prop="repayment_amount_all"
                                     align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天收回的金额" placement="top-start">-->
                            <div class="cell">
                                {{ parseFloat(scope.row.repayment_amount_all).toFixed(2) }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="已还单数" :render-header="renderHeader" prop="repayment_count_all"
                                     align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天收回的单数" placement="top-start">-->
                            <div class="cell">
                                {{ scope.row.repayment_count_all }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="未还金额" :render-header="renderHeader" prop="un_repayment_amount_all"
                                     align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天应还，但是未还的金额" placement="top-start">-->
                            <div class="cell">
                                {{ parseFloat(scope.row.un_repayment_amount_all).toFixed(2) }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="未还单数" :render-header="renderHeader" prop="un_repayment_count"
                                     align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天应还，但是未还的单数" placement="top-start">-->
                            <div class="cell">
                                {{ scope.row.un_repayment_count }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="逾期率" :render-header="renderHeader" align="center">
                        <template slot-scope="scope">
                            <!--<el-tooltip class="item" effect="dark" content="当天的逾期率" placement="top-start">-->
                            <div class="cell">
                                {{ scope.row.count_all?(scope.row.un_repayment_count / scope.row.count_all)*100+'%':'0%'
                                }}
                            </div>
                            <!--</el-tooltip>-->
                        </template>
                    </el-table-column>
                </el-table-column>
                <el-table-column label="收益" align="center" :render-header="renderHeader">
                    <template slot-scope="scope">
                        <!--<el-tooltip class="item" effect="dark" content="当天收回金额-当天放出去的金额=当天收益" placement="top-start">-->
                        <div class="cell">
                            {{ parseFloat(scope.row.repayment_amount_all - scope.row.loan_amount_all).toFixed(2) }}
                        </div>
                        <!--</el-tooltip>-->
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination layout="total,prev, pager, next" class="fr" :total="reportList.dataCount" :pageSize="15"
                           @current-change="getDataByPage">
            </el-pagination>
        </el-card>
    </div>
</template>
<script>
    import http from 'src/assets/js/http'

    export default {
        data() {
            return {
                condition: {
                    type: 2, //1用于请求天数据 2为月数据
                    page: 1,
                    limit: 15,
                    structure_id: '',
                    uid: ''
                },
                post_id: JSON.parse(localStorage.getItem('userInfo')).data.post_id,
                reportList: {},
                groupList: {},
                groupMemberList: {},
            }
        },
        watch: {
            'condition.structure_id': function () {
                this.getGroupMember();
            }
        },
        methods: {
            getReportList() {
                this.apiGet('/admin/report', {params: this.condition}).then((res) => {
                    if (res.code === 200) {
                        this.$set(this, 'reportList', res.data)
                    } else {
                        _g.toastMsg('error', res.error)
                    }
                }).catch((err) => {
                    _g.toastMsg('error', err)
                })
            },
            getDataByPage(page) {
                this.$set(this.condition, 'page', page)
                this.getReportList();
            },
            getGroup() {
                this.apiGet('/admin/Structures/getGroup').then((res) => {
                    if (res.code === 200) {
                        this.$set(this, 'groupList', res.data)
                    } else {
                        _g.toastMsg('error', res.error)
                    }
                }).catch((err) => {
                    _g.toastMsg('error', err)
                })
            },
            getGroupMember() {
                this.apiGet('/admin/users', {params: {structure_id: this.condition.structure_id}}).then((res) => {
                    if (res.code === 200) {
                        this.$set(this, 'groupMemberList', res.data)
                    } else {
                        _g.toastMsg('error', res.error)
                    }
                }).catch((err) => {
                    _g.toastMsg('error', err)
                })
            },
            renderHeader(h, {column,$index}) {
                let data = {
                    "放款金额": '当天放款的本金',
                    "服务费": '当天放款的服务费',
                    "应还总额": '当天放款金额的应收回金额',
                    "放款单数": '当天放款的单数',
                    "总支出": '当天放款本金',
                    "已还金额": '当天收回的金额',
                    "已还单数": '当天收回的单数',
                    "未还金额": '当天应还，但是未还的金额',
                    "未还单数": '当天应还，但是未还的单数',
                    "逾期率": '当天的逾期率',
                    "收益": '当天收回金额-当天放出去的金额=当天收益',
                }
                if (column.label) {
                    return h('span', {}, [
                        h('span', {}, column.label),
                        h('el-popover', {props: {placement: 'top-start', width: '200', trigger: 'hover', content: data[column.label]}}, [
                            h('i', {slot: 'reference', class: 'el-icon-question', style: 'color:gray;font-size:16px;margin-left:10px;'}, '')
                        ])
                    ])
                }
            }
        },
        mounted() {
            this.getReportList();
            _g.closeGlobalLoading();
            if (this.post_id !== 2) {
                this.getGroup();
            }
        },
        mixins: [http]
    }
</script>


